import fetch from 'isomorphic-unfetch';
import AppBar from '@material-ui/core/AppBar';
import ReactMarkdown from 'react-markdown';

class Skills extends React.Component {
  getSkillsByDomain = (domain) => {
    const domainSkills = this.props.skills.filter(skill => {
      console.log(skill);
      skill.domaine.id = domain;
    });

    return <div>
      {domainSkills.map(skill => (
        <div key={skill.id}>
          <h3>{skill['Titre']}</h3>
        </div>
      ))}
    </div>
  };

  render() {
    const { classes, hp, lang, domains } = this.props;

    return (
      <div id="section-skills">
        <AppBar position="static" className={classes.appbar}>
          {hp['Titre compétences']}
        </AppBar>
        <ReactMarkdown source={hp['Texte compétences']} className={classes.markdownskills} />
        {domains.map(domain => (
          <div key={domain.id}>
            <h2>{lang === 'en' ? domain['Titre anglais'] : domain['Titre']}</h2>
            <div>{this.getSkillsByDomain(domain.id)}</div>
          </div>
        ))}
      </div>
    );
  }
}

export default Skills;
